/* Slider */
.slick-slider {
  // position: relative;
  display: block;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: help;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  display: table;
  content: '';
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

// custom styles

.slick-dots {
  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    &.slick-active {
      button {
        background-color: transparent;
        border-color: #fff;
        width: 17px;
        height: 17px;
      }

    }

    button {
      box-sizing: inherit;
      font-size: 0;
      line-height: 0;
      display: block;
      width: 15px;
      height: 15px;
      padding: 0;
      cursor: pointer;
      color: transparent;
      outline: none;
      border-width: 2px;
      border-color: transparent;
      border-style: solid;
      border-radius: 50%;
      background-color: #fff;
    }

  }

}

.slick-prev,
.slick-next {
  position: absolute;
  top: 0;
  display: block;
  height: 40px;
  width: 40px;
  font-size: 0px;
  cursor: pointer;
  color: transparent;
  padding: 0;
  border: none;
  outline: none;
  z-index: 1;
}

.slick-prev {
  left: 0;
  background: linear-gradient(to right, rgba(46, 66, 84, 1) 0%, rgba(46, 66, 84, 0.5) 100%);

  &:before {
    background-image: $menu-arrow-left;
  }

}

.slick-next {
  right: 0;
  background: linear-gradient(to left, rgba(46, 66, 84, 1) 0%, rgba(46, 66, 84, 0.5) 100%);

  &:before {
    background-image: $menu-arrow-right;
  }

}

.slick-prev:before,
.slick-next:before {
  content: '';
  width: 40px;
  height: 40px;
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}